$color : rgb(202, 202, 202);
.App {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    margin-top: 50px;
    h1 {
        font-weight: 400;
        color: #ab59598f;

    }
    .todos {
        margin: 0 auto;
        margin-top: 10px;
        width: 430px;
        .input {
            width: 100%;
            padding: 18px;
            padding-left: 25px;
            border: none;
            outline: none;
            margin-bottom: 3px;
        }
        .form {
            display: flex;
            align-items: center;
            padding-left: 18px;
            background-color: #fff;
            margin-bottom: 2px;
            svg {
                transform: rotate(180deg);
            }
        }
        .label {
            width: 30px;
            height: 30px;
            border-radius: 50%;
            border: 1px solid $color ;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .list {
            width: 100%;
            display: flex;
            flex-direction: column;
        }
        .todo {
            text-align: left;
            padding: 10px;
            background-color: #fff;
            border-bottom: 1px solid $color ;
            position: relative;
            display: flex;
            align-items: center;
            gap: 20px;
            cursor: pointer;
            padding-right: 15px;
            position: relative;
            h3 {
                max-width: 350px;
                overflow-wrap: break-word;
            }
            .times {
                position: absolute;
                right: 4px;
                top: 50%;
                transform: translateY(-50%);
                cursor: pointer;
            }
        }
    }
}
::-webkit-input-placeholder {
    color: $color ;
}


.nav {
    background-color: #fff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    button {
        background-color: #fff;
        border: 1px solid transparent;
        cursor: pointer;
        padding: 5px;
        border-radius: 5px;
    }
}